import "./UserPortal.css";
import React, { Component, PureComponent } from 'react';
import { BrowserRouter as Router, Route, Switch, Link} from "react-router-dom";
import Highchart from '../../components/Graphs/Highchart';
import Highmap from '../../components/Graphs/Map';
import Impact from '../../images/impactReport.jpg';
import DonationForm from '../../components/DonationForm';
import Heatmap from '../../images/heatmap.jpg';



class UserPortal extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      user: {
        first_name: 'Guest'
      }
    };
  }

  componentWillReceiveProps(nextProps) {
    if(this.props.user != nextProps.user) {
      this.setState({user: nextProps.user})
      console.log('there is a props.user')
      console.log(this.state.user)
    }
    console.log('there is not a props.user')
  }
 
  render() {
    console.log(this.props)
    return (
      <div className="userPortalContainer">
         <div className='welcomeHeader'>
           <h1>Hello, {this.state.user.first_name}!</h1>
           <span className='userInfo'>
             <h3>
               Your latest contributions have helped [enter demographic here]!
             </h3>
             <h3>
               [total contributions]
             </h3>
          </span>
        </div>

        <div id='highchart'>
          <Highchart />
        </div>

        <div className='userContainer'>
          <div id='userMap'>
            <img src={Heatmap} />
          </div>

          <div className='donorPortal'>
            <DonationForm />
          </div>
        </div>

        <div>
          <img id='impact' src={Impact} />
        </div>
      </div>
    );
  }
}


export default UserPortal;



